export interface User {
    id: number | null,
    email: string,
    isActive: boolean | null,
    name: string,
    displayName: string | null,
    profilePic: string,
    displayLocationName?: string
}

export interface Query {
    id: number,
    queryType: string,
    question: string,
    answer: string;
    attachments: string;
    createdAt: string;
    updatedAt: string;
    queryFor: string | null;
    isActive: boolean
}

export interface UserState {
    data: any;
}

